import { createArticles, updateArticles } from '@/api/article'
import { getArticle } from '@/api/article'


export default {
  name: "editor",
  middleware: 'auth',
  data() {
    return {
      article: {
        "title": "",
        "description": "",
        "body": "",
        "tagList": ''
      }
    }
  },
  async mounted() {
    if (this.$route.params && this.$route.params.slug) {
      const { data } = await getArticle(this.$route.params.slug)
      const { article } = data
      // const md = new MarkdownIt()
      // article.body = md.render(article.body)

      const {
        title,
        description,
        body,
        tagList
      } = article
      
      this.article = {
        title,
        description,
        body,
        tagList: '',
        prevTagList: tagList
      }
    }
  },
  methods: {
    async handleSubmit() {
      try {
        const { data } = await createArticles({
          article: {
            ...this.article,
            tagList: [this.article.tagList].concat(this.article.prevTagList || [])
          }
        })

        this.$router.push(`/article/${data.article.slug}`)

      } catch(err) {
        conosle.error(err)
      }
    }
  }
}
